import React, { Component } from 'react';
import { connect } from 'react-redux'
import './QuestionnaireViewer.styles.scss'

import { eBill, contact, name } from '../../../redux/quoteForm/quoteForm.actions';

class QuestionnaireViewer extends Component {

    constructor(props) {
        super(props)
    }
    
    render() {

        const {onClick} = this.props

        return (
            <section className='QuestionnaireViewer' onClick={onClick} >
                <section className='QuestionnaireViewer__Container' onClick={e => e.stopPropagation()} >
                    <section className='QuestionnaireViewer__Container__Top' >
    
                    </section>
                    <section className='QuestionnaireViewer__Container__Bottom' >
    
                    </section>
                    <p className='QuestionnaireViewer__Container__X' onClick={onClick} >X</p>
                </section>
            </section>
        );
    }
};

const mapStateToProps = ({ quoteForm: { ownsHome, zipCode, eBill, contact, name } }) => ({
    ownsHome,
    zipCode,
    eBill,
    contact,
    name
})

const mapDispathToProps = dispatch => ({
    eBill: (billAmount) => dispatch(eBill(billAmount)),
    contact: (contactOptions) => dispatch(contact(contactOptions)),
    name: (names) => dispatch(name(names))
})

export default connect(mapStateToProps, mapDispathToProps)(QuestionnaireViewer);